/* Sidebar.module.css */
.sidebar {
  width: 256px;
  height: 100vh;
  background-color: #f9fafb;
  border-right: 1px solid #e5e7eb;
  display: flex;
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

/* Logo Section */
.logo-section {
  padding: 16px;
  border-bottom: 1px solid #e5e7eb;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo-icon {
  width: 40px;
  height: 40px;
  background-color: #fbbf24;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo-text {
  color: #6b7280;
  font-weight: 500;
  font-size: 14px;
}

/* Menu Section */
.menu-section {
  flex: 1;
  padding: 16px 0;
}

.menu-nav {
  padding: 0 12px;
}

.menu-item {
  width: 100%;
  text-align: left;
  padding: 10px 12px;
  font-size: 14px;
  color: #374151;
  background: transparent;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  margin-bottom: 4px;
  transition: background-color 0.15s ease;
}

.menu-item:hover {
  background-color: #f3f4f6;
}

/* Dashboard special styling */
.dashboard-item {
  background-color: white;
  border: 1px solid #d1d5db;
}

.dashboard-item:hover {
  background-color: #f9fafb;
}

/* Dropdown */
.dropdown-container {
  margin-bottom: 4px;
}

.dropdown-trigger {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dropdown-arrow {
  font-size: 12px;
  transition: transform 0.2s ease;
}

.dropdown-arrow.open {
  transform: rotate(180deg);
}

/* User Section */
.user-section {
  border-top: 1px solid #e5e7eb;
  padding: 12px;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  font-size: 14px;
  color: #374151;
  margin-bottom: 4px;
}

.user-avatar {
  width: 24px;
  height: 24px;
  background-color: #d1d5db;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

.user-name {
  font-size: 14px;
}

.logout-button {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  font-size: 14px;
  color: #374151;
  background: transparent;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.15s ease;
}

.logout-button:hover {
  background-color: #f3f4f6;
}

.logout-icon {
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}